import * as React from "react";
import { motion } from "framer-motion";
// import Planet2  from '../images/planet-2.svg';

const variants = {
  open: {
    y: 0,
    opacity: 1,
    transition: {
      y: { stiffness: 1000, velocity: -100 }
    }
  },
  closed: {
    y: 50,
    opacity: 0,
    transition: {
      y: { stiffness: 1000 }
    }
  }
};

const colors = ["#FF008C", "#D309E1", "#9C1AFF", "#7700FF", "#4400FF"];
const href = ['Sports','bob','vli','red','gro']
// const img =  ['ivan','bob','vli','red','gro']
// const alt =  ['ivan','bob','vli','red','gro']

export const MenuItem = ({ i }) => {
  const style = { border: `2px solid ${colors[i]}`, backgroundColor: `white` };
  return (
    <motion.li 
      variants={variants}
      whileHover={{ scale: 1.1 }}
      whileTap={{ scale: 0.95 }}
    >

      {/* <div className="icon-placeholder" style={style}>
        <img src={img[i]} alt={alt[i]} />
      </div> */}
      <div className="text-placeholder" style={style}>
        <a style={{textDecoration:'none'}} href={`${href[i]}`}> {`${href[i]}`}</a>
      </div>
    </motion.li>
  );
};
